<template>
  <div>
    <h2>工作经历</h2>
    <el-form>
      <div class="container" v-for="(work, index) in workHistory" :key="index">
        <el-form-item label="公司">
          <el-input v-model="work.company"></el-input>
        </el-form-item>
        <el-form-item label="工作内容">
          <el-input v-model="work.content"></el-input>
        </el-form-item>
        <i class="el-icon-circle-close" v-on:click="removeWorkHistory(index)"></i>
        <!--对应上面的index 表示work的序号-->
        <hr />
      </div>
    </el-form>
    <el-row>
      <el-button type="primary" v-on:click="addWorkHistory">主要按钮</el-button>
    </el-row>
  </div>
</template>

<script>
export default {
  props: ["workHistory"],
  methods: {
    addWorkHistory() {
      this.workHistory.push({
        company: "",
        content: ""
      });
    },
    removeWorkHistory(index) {
      this.workHistory.splice(index, 1); // 删除从index开始的1个
    }
  }
};
</script>

